<template>
  <div class="controls">
    <div
      class="controls_results_container"
      :class="{'controls_results_container-active' : resultsActive}"
      @click="resultsActive = false"
    >
      <template v-if="game.results.attacker > -1">
        <div class="controls_results">
          <div
            class="controls_results_side"
            :class="{'controls_results_side-win' : game.results.attackerDice.total > game.results.defenderDice.total}"
          >
            <div class="controls_results_result">
              <div class="controls_results_total">{{game.results.attackerDice.total}}</div>
              <div class="controls_results_dices">
                <div
                  class="controls_results_dice"
                  :class="['controls_results_dice-' + dice]"
                  v-for="(dice, i) in game.results.attackerDice.dice"
                  :key="i"
                ></div>
              </div>
            </div>
            <div
              class="controls_results_color"
              :class="['controls_player_color-' + game.results.attacker]"
            ></div>
          </div>
          <div class="controls_results_vs">VS</div>
          <div
            class="controls_results_side"
            :class="{'controls_results_side-win' : game.results.attackerDice.total <= game.results.defenderDice.total}"
          >
            <div
              class="controls_results_color"
              :class="['controls_player_color-' + game.results.defender]"
            ></div>
            <div class="controls_results_result">
              <div class="controls_results_total">{{game.results.defenderDice.total}}</div>
              <div class="controls_results_dices">
                <div
                  class="controls_results_dice"
                  :class="['controls_results_dice-' + dice]"
                  v-for="(dice, i) in game.results.defenderDice.dice"
                  :key="i"
                ></div>
              </div>
            </div>
          </div>
        </div>
      </template>
    </div>
    <div class="controls_players">
      <div
        v-for="(player) in game.players"
        class="controls_player"
        :class="{'controls_player-active': player.id === game.player.id}"
        :key="player.id"
      >
        <div class="controls_player_color" :class="['controls_player_color-' + player.id]"></div>
        <div class="controls_player_size">{{player.size}}</div>
        <div class="controls_player_reserve">(+{{player.reserve}})</div>
      </div>
    </div>
    <div class="controls_actions">
      <v-btn color="accent" @click="nextPlayer">Next turn</v-btn>
      <v-btn color="accent" outlined @click="newGame">New game</v-btn>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'

export default {
  name: 'GameControls',
  computed: mapState({
    game: state => state.game,
    gameResults: state => state.game.results
  }),
  data: () => ({
    gameService: new GameService(),
    resultsActive: false
  }),
  methods: {
    nextPlayer () {
      this.gameService.nextPlayer()
    },
    newGame () {
      this.gameService.startGame()
    }
  },
  watch: {
    gameResults () {
      this.resultsActive = true
    }
  }
}
</script>

