<template>
  <div class="game">
    <div class="game_board" v-bind:style="{ width: (game.settings.size * 100 + 4) + 'px' }">
      <div
        v-for="(cell, i) in game.board"
        :key="i"
        class="game_cell"
        :class="[
        {'game_cell-selectable' : selectable(i, cell.owner, cell.dice)},
        {'game_cell-selected' : i === game.selected},
        {'game_cell-borderTop' : borderTop(i, cell.owner)},
        {'game_cell-borderRight' : borderRight(i, cell.owner)},
        {'game_cell-borderBottom' : borderBottom(i, cell.owner)},
        {'game_cell-borderLeft' : borderLeft(i, cell.owner)},
        'game_cell-owner-' + cell.owner]"
        @click="select(i, cell.owner)"
        :depressed="i !== game.selected"
        v-ripple
      >
      <div class="game_cell_dices">
        <div v-for="(dice, i) in cell.dice" :key="i" class="game_cell_dice"></div>
      </div>
      </div>
    </div>
    <GameControls />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'
import GameControls from './GameControls.vue'

export default {
  name: 'Game',
  components: {
    GameControls
  },
  computed: mapState({
    game: state => state.game
  }),
  data: () => ({
    gameService: new GameService()
  }),
  methods: {
    select (i, owner) {
      if (owner === this.game.player.id) {
        this.gameService.changeSelected(i)
      } else {
        this.gameService.attack(i)
      }
    },
    selectable (i, owner, dice) {
      const isPlayer = owner === this.game.player.id && dice > 1
      const adjacent =
        this.game.selected !== null
          ? this.game.adjacents[this.game.selected].includes(i)
          : false
      return isPlayer || adjacent
    },
    borderTop (i, owner) {
      const checkId = i - this.game.settings.size
      if (
        i >= this.game.settings.size &&
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return false
      }
      return true
    },
    borderRight (i, owner) {
      const checkId = i + 1
      if (
        checkId % this.game.settings.size &&
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return false
      }
      return true
    },
    borderBottom (i, owner) {
      const checkId = i + this.game.settings.size
      if (
        i < this.game.settings.size * (this.game.settings.size - 1) &&
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return false
      }
      return true
    },
    borderLeft (i, owner) {
      const checkId = i - 1
      if (
        i % this.game.settings.size &&
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return false
      }
      return true
    }
  }
}
</script>

